
import RsMenu from './RsMenu.vue';
import RsSubMenu from './RsSubMenu.vue';
import RsMenuItem from './RsMenuItem.vue';
import './rsmenu.scss';

RsMenu.install = function(Vue) {

    Vue.component(RsMenu.name, RsMenu);
    Vue.component(RsSubMenu.name, RsSubMenu);
    Vue.component(RsMenuItem.name, RsMenuItem);
};

export default RsMenu;